.header {
	padding: 46px 0 56px;
}
.container {
}
.header_row {
	display: flex;
	column-gap:30px;
	align-items: center;
	justify-content: space-between;
}
.header__logo {
	display: flex;
	align-items: center;

}
.header__nav {

}

.header__nav ul{
	display: flex;
	column-gap: 65px;
	align-items: center;
}

.header__nav a{
	color: var(--black);
	text-transform: uppercase;
	font-weight: 500;
	font-size: 22px;
	letter-spacing: 0.01em;
}

a.header__link:hover{
	text-decoration: underline;
	transition: all 0.5s;

}


a.header__nav-btn{
	background-color: var(--black);
	color: var(--white);
	padding:17px 22px;
	border-radius: 10px;
	font-size: 24px;
}

.header__nav-btn:hover{
	background-color: rgb(255, 14, 14);
	transition: all 0.3s;
}