.container {
}
.promo__content {
	background-color: #f4f6f5;
	border-radius: 53px;
	
	display: flex;

	column-gap: 24px;
	padding-top: 68px;
	padding-left: 120px;
}
.promo__text {
}
.promo__image {
}

.promo__text {
}
.promo__title {
	max-width: 480px;
	font-weight: 900;
	font-size: 96px;
	line-height: 125%;
	color: var(--black);

	margin-bottom: 40px;
	
}

.highlight{
	position: relative;
	display: inline-block;
}

.highlight span{
	position: relative;
	z-index: 2;
}

.highlight::before{
	content: '';
	display: block;
	position: absolute;
	top: -12px;
	bottom: -12px;
	left: -20px;
	right: -60px;
	background-color: var(--white);
	transform: rotate(-2deg);
}
.highlight-y::before{
	background-color: var(--light-yellow);
	right: -110px;
}

.promo__subtitle {
	font-weight: 400;
	font-size: 32px;
	letter-spacing: -0.05em;
	color: var(--black);

	margin-bottom: 32px;
}
.promo__bottom {
	display: flex;
	column-gap: 34px;
	align-items: center;
}
img.promo__bottom-img {
	display: inline-block;
	width: 90px;
	height: 32px; 
}
.promo__btn {
	font-weight: 500;
	font-size: 30px;
	letter-spacing: -0.05em;
	color: var(--white);
	border-radius: 10px;
	background-color: var(--black);
	padding:18px 40px;
}

.promo__btn:hover{
	background-color: rgb(255, 14, 14);
	transition: all 0.3s;
}