.card {
	position: relative;
}

.card__link{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.card:hover .card__img{
	animation: opacity 0.3s ease-in-out;
}
@keyframes opacity{
	0%{opacity: 1;}
	50%{opacity: 0.8;}
	100%{opacity: 1;}
}

@keyframes slide{
	0%{left: 0;}
	50%{left:-20px;}
	100%{left: 0;}
}

.card__img{
margin-bottom: 28px;
border-radius: 20px;
cursor: pointer;
}


.card__body {
	display: flex;
	justify-content: space-between;
	align-items: center;
	column-gap: 10px;
}
.card__text{
}
.card__title {
	margin-bottom: 7px;
	font-weight: 500;
	font-size: 32px;
	line-height: 156%;
	letter-spacing: -0.04em;
	color: var(--black);
}
.card__muted {
	font-weight: 500;
	font-size: 24px;
	line-height: 208%;
	letter-spacing: -0.04em;
	color: #7f7f7f;
}
.card:hover .card__icon{
	animation: slide 0.3s ease-in-out;
}

.card__icon{
	position: relative;
}