.footer {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	background: #000;
	color: var(--white);
	padding: 120px 100px;
	align-items: center;
}


.footer__socials {
	display: flex;
	flex-direction: column;
	row-gap: 32px;
}
.footer__title {
	font-weight: 900;
	font-size: 40px;
	letter-spacing: 0.03em;
	color: #fff;

}
.footer__text {
	font-weight: 400;
	font-size: 20px;
	line-height: 200%;
	letter-spacing: 0.02em;
	color: #8e8e8e;
	max-width: 415px;
}
.footer__icons {
	display: flex;
	column-gap: 14px;
}
.footer__item {
	width: 52px;
	height: 52px;
	background-color: var(--light-yellow);
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 30%;

}
.footer__item:hover{
	background-color: rgb(250, 250, 78);
	cursor: pointer;
	transition: all 0.3s;
}

img .footer__icon {
	width: 100%;
	display: inline-block;
}



.footer__navigation {
	display: flex;
	column-gap: 20px;
	justify-content: space-between;
}
.footer__bar {
	
}
.bar {

}
.bar__title {
	font-weight: 400;
	font-size: 20px;
	line-height: 200%;
	letter-spacing: 0.02em;
	color: #d9d9d9;
	margin-bottom: 32px;
}
.bar__list {
	display: flex;
	flex-direction: column;
	row-gap: 32px;
}
.bar__item {
}
a.bar__link {
	font-weight: 400;
	font-size: 20px;
	line-height: 200%;
	letter-spacing: 0.02em;
	color: #8e8e8e;
}

a.bar__link:hover{
	color: white;
	transition: all 0.3s;
	text-decoration: underline;
}

