.sale {
	margin-bottom: 50px;

}
.container {
}
.sale__content {
	background:  linear-gradient(360deg, #e0c340 0%, #dfc23e 3.93%, #e1c441 7.23%, #e3c743 10.23%, #e4c542 13.14%, #e6c744 16.22%, #e7c845 19.84%, #e5c643 24.38%, #e6c945 31%, #e3c743 37.19%, #e9ca48 43.33%, #edce49 50.36%, #f0d44c 56.57%, #f4d84f 62.71%, #f6da52 67.12%, #f7db53 73.77%, #f9dd55 79.08%, #f9df56 85.64%, #fae157 93.5%, #f9df56 100%);
	display: flex;

	column-gap: 60px;
	/* min-height: 968px; */
	max-width: 100%;

	
	/* padding-top: 120px; */
	/* padding-left: 120px; */
}
.sale__image {
	max-width: 100%;
}
.sale__text {
	/* display: flex;
	flex-direction: column; */
	padding-top: 130px;
	padding-right: 200px;
	max-width: 750px;
	

}
.sale__title {
	max-width: 540px;
	font-weight: 900;
	font-size: 96px;
	line-height: 125%;
	color: var(--black);
	max-width: 700px;

	margin-bottom: 40px;
	
}
.highlight {
	content: '';
	display: block;
	position: absolute;
	top: -12px;
	bottom: -12px;
	left: -20px;
	right: -60px;
	background-color: var(--white);
	transform: rotate(-2deg);
}
.highlight{
	position: relative;
	display: inline-block;
}

.highlight span{
	position: relative;
	z-index: 2;
}

.sale__subtitle {
	font-weight: 400;
	font-size: 32px;
	letter-spacing: -0.05em;
	color: var(--black);
	margin-bottom: 40px;
}
.sale__terms  {
	font-weight: 400;
	font-size: 36px;
	line-height: 145%;
	letter-spacing: -0.02em;
	margin-bottom: 40px;
}

.strong{
	font-weight: 700;
	font-size: 36px;
	line-height: 145%;
	letter-spacing: 0.02em;
	color: #000;
}
.sale__btn {

	font-weight: 500;
	font-size: 30px;
	letter-spacing: -0.05em;
	color: var(--white);
	border-radius: 10px;
	background-color: var(--black);
	padding:18px 40px;
}

.sale__btn:hover{
	background-color: rgb(255, 14, 14);
	transition: all 0.3s;
}




