:root{
	--light-yellow: #ebd96b;
	--yellow:#e5c643;
	--black:#000;
	--white:#fefefe;
}

body{
	font-family: 'Poppins', sans-serif;
	color: var(--black);
}

.container{
	max-width: 1720px;
	margin: 0 auto;
	padding: 0 30px;

}

.title-2{
	display: inline-block;
	position: relative;
	font-weight: 900;
	font-size: 48px;
	line-height: 104%;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	color: var(--black);
}

.title-2::after{
	z-index: -1;
	content: '';
	position: absolute;
	right: 0;
	bottom: 0;
	width: 190px;
	height: 32px;;
	background: url('./../img/arrivals/decor.svg') no-repeat;
	
}